body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2c2915; /* Dark olive - enforce background color */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Forest Animation Styles */
.falling-leaves {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -5;
  opacity: 0.3;
}

.leaf {
  position: absolute;
  top: -10%;
  background-color: rgba(94, 140, 49, 0.5);
  border-radius: 50% 0;
  transform: rotate(45deg);
  animation: fall linear infinite;
}

.leaf1 {
  background-color: rgba(94, 140, 49, 0.5);
}

.leaf2 {
  background-color: rgba(156, 85, 24, 0.5);
}

.leaf3 {
  background-color: rgba(204, 156, 58, 0.5);
}

@keyframes fall {
  0% {
    top: -10%;
    transform: rotate(45deg) translateX(0);
    opacity: 0.5;
  }
  
  25% {
    transform: rotate(90deg) translateX(20px);
  }
  
  50% {
    transform: rotate(135deg) translateX(-20px);
  }
  
  75% {
    transform: rotate(180deg) translateX(20px);
  }
  
  100% {
    top: 110%;
    transform: rotate(225deg) translateX(-20px);
    opacity: 0;
  }
}

.fireflies {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -4;
  opacity: 0.5;
}

.firefly {
  position: absolute;
  background-color: rgba(255, 230, 150, 0.6);
  border-radius: 50%;
  box-shadow: 0 0 3px 3px rgba(255, 230, 150, 0.2);
  animation: firefly 3s ease-in-out infinite;
}

@keyframes firefly {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 0.7;
  }
}

.animals {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: -2;
  opacity: 0.2;
}

.fox {
  position: absolute;
  bottom: 20px;
  right: 5%;
  width: 60px;
  height: 40px;
  background-color: rgba(156, 85, 24, 0.2);
  border-radius: 50% 40% 40% 50%;
}

.fox::before {
  content: '';
  position: absolute;
  top: -15px;
  right: 10px;
  width: 20px;
  height: 20px;
  background-color: rgba(156, 85, 24, 0.2);
  border-radius: 50% 50% 0 0;
}

.owl {
  position: absolute;
  top: 15%;
  left: 10%;
  width: 30px;
  height: 50px;
  background-color: rgba(100, 100, 100, 0.2);
  border-radius: 50% 50% 50% 50%;
}

.owl::before {
  content: '';
  position: absolute;
  top: -10px;
  left: 5px;
  width: 10px;
  height: 10px;
  background-color: rgba(100, 100, 100, 0.2);
  border-radius: 50%;
}

.owl::after {
  content: '';
  position: absolute;
  top: -10px;
  right: 5px;
  width: 10px;
  height: 10px;
  background-color: rgba(100, 100, 100, 0.2);
  border-radius: 50%;
}

.rabbit {
  position: absolute;
  bottom: 10px;
  left: 15%;
  width: 30px;
  height: 20px;
  background-color: rgba(200, 200, 200, 0.2);
  border-radius: 50%;
}

.rabbit::before {
  content: '';
  position: absolute;
  top: -15px;
  left: 5%;
  width: 8px;
  height: 20px;
  background-color: rgba(200, 200, 200, 0.2);
  border-radius: 50% 50% 0 0;
}

.rabbit::after {
  content: '';
  position: absolute;
  top: -15px;
  left: 17px;
  width: 8px;
  height: 20px;
  background-color: rgba(200, 200, 200, 0.2);
  border-radius: 50% 50% 0 0;
}

.ambient-light {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at top, rgba(104, 155, 55, 0.05) 0%, rgba(44, 41, 21, 0.05) 100%);
  pointer-events: none;
  z-index: -3;
  opacity: 0.1;
}